import axios from "axios";
import { useContext, useEffect, useState } from "react";

import { WebsiteContext } from "../../App";
import { BlogInterface } from "./interface";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { GoogleAds } from "../../ads";
import { DateTime } from "ts-luxon";

interface BlogListsProps {
  filter?:
    | {
        [key: string]: string;
      }
    | undefined;

  setFilter?: React.Dispatch<
    React.SetStateAction<
      | {
          [key: string]: string;
        }
      | undefined
    >
  >;
}

export const BlogLists: React.FC<BlogListsProps> = ({ filter, setFilter }) => {
  const [blogs, setBlogs] = useState<BlogInterface[]>();
  const website = useContext(WebsiteContext);

  const data = useSelector((state: any) => state.profile);

  useEffect(() => {
    if (!website?.domain) return;
    getAllBlogs();
  }, [website]);

  const getAllBlogs = async () => {
    const data = await axios.get(
      `sites/${website?.domain}.wordpress.com/posts`,
      {
        params: { page: 0, ...filter },
      }
    );
    setBlogs(data?.data?.posts);
  };

  useEffect(() => {
    getAllBlogs();
  }, [filter]);

  const loadMore = (e: any) => {
    console.log(e);
  };

  return (
    <>
      <GoogleAds />
      {blogs?.map((blog) => (
        <article className="row mb-5" key={blog?.ID}>
          <div className="col-md-4 mb-4 mb-md-0">
            <div className="post-slider slider-sm">
              <img
                loading="lazy"
                src={`${data?.icon}`}
                className="img-fluid"
                alt="post-thumb"
                style={{ height: "200px", objectFit: "cover" }}
              />
            </div>
          </div>
          <div className="col-md-8">
            <h3 className="h5">
              <Link className="post-title" to={`/blog/${blog?.slug}`}>
                {blog?.title}
              </Link>
            </h3>
            <ul className="list-inline post-meta mb-2">
              <li className="list-inline-item">
                <i className="ti-user mr-2" />
                <Link to="/about">{blog?.author?.name}</Link>
              </li>
              <li className="list-inline-item">
                Date :{" "}
                {DateTime.fromISO(blog?.date).toFormat("dd-MM-yyyy HH:mm a")}
              </li>
              <li className="list-inline-item">
                Categories :
                {Object.values(blog?.categories)?.map((category) => (
                  <Link
                    to={`/category/${category?.slug}`}
                    key={category?.name}
                    className="ml-1"
                  >
                    {category?.name}
                  </Link>
                ))}
              </li>
            </ul>
            <p dangerouslySetInnerHTML={{ __html: blog?.excerpt }}></p>
            <Link
              to={`/blog/${blog?.slug}`}
              className="btn btn-outline-primary"
            >
              Continue Reading
            </Link>
          </div>
        </article>
      ))}
    </>
  );
};
