import { Link, Outlet } from "react-router-dom";

import { NewsLetter } from "./news-letter";
import { useSelector } from "react-redux";

export const AppLayout = () => {
  const date = new Date().getFullYear();

  const data = useSelector((state: any) => state.profile);

  return (
    <>
      <header className="sticky-top bg-white border-bottom border-default">
        <div className="container">
          <nav className="navbar navbar-expand-lg navbar-white">
            <Link className="navbar-brand" to="/">
              <img
                className="img-fluid"
                width="70px"
                src={
                  data?.icon ||
                  "https://cloudstarmohan.files.wordpress.com/2018/09/yv7aw.jpg?w=200"
                }
                alt="Cloudstar Mohan"
              />
            </Link>
            <button
              className="navbar-toggler border-0"
              type="button"
              data-toggle="collapse"
              data-target="#navigation"
            >
              <i className="ti-menu" />
            </button>
            <div
              className="collapse navbar-collapse text-center"
              id="navigation"
            >
              <ul className="navbar-nav ml-auto">
                <li className="nav-item">
                  <Link className="nav-link" to="/">
                    Home
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="gallery">
                    Gallery
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="about">
                    About
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="contact">
                    Contact
                  </Link>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      </header>
      <section className="section">
        <div className="container">
          <Outlet />
        </div>
      </section>
      <footer className="section-sm pb-0 border-top border-default">
        <div className="container">
          <div className="row justify-content-between">
            <div className="col-md-3 mb-4">
              <Link className="mb-4 d-block" to="/">
                <img
                  className="img-fluid"
                  width="60px"
                  src={
                    data?.icon ||
                    "https://cloudstarmohan.files.wordpress.com/2018/09/yv7aw.jpg?w=200"
                  }
                  alt="Cloudstar Mohan"
                />
              </Link>
              <p>{data?.description}</p>
            </div>
            <div className="col-lg-2 col-md-3 col-6 mb-4">
              <h6 className="mb-4">Quick Links</h6>
              <ul className="list-unstyled footer-list">
                <li>
                  <Link to="about">About</Link>
                </li>
                <li>
                  <Link to="contact">Contact</Link>
                </li>
                <li>
                  <Link to="privacy-policy">Privacy Policy</Link>
                </li>
                <li>
                  <Link to="terms-conditions">Terms Conditions</Link>
                </li>
              </ul>
            </div>
            <div className="col-lg-2 col-md-3 col-6 mb-4">
              <h6 className="mb-4">Social Links</h6>
              <ul className="list-unstyled footer-list">
                <li>
                  <Link
                    to={"https://www.facebook.com/cloudstarmohan"}
                    target="_blank"
                  >
                    facebook
                  </Link>
                </li>
                <li>
                  <Link to="https://twitter.com/cloudstarmohan" target="_blank">
                    twitter
                  </Link>
                </li>
                <li>
                  <Link
                    to="http://ca.linkedin.com/in/cloudstarmohan"
                    target="_blank"
                  >
                    linkedin
                  </Link>
                </li>
                <li>
                  <Link to={"https://github.com/spmohansp"} target="_blank">
                    github
                  </Link>
                </li>
              </ul>
            </div>
            <div className="col-md-3 mb-4">
              <h6 className="mb-4">Change Domain</h6>
              <NewsLetter />
            </div>
          </div>
          <div className="scroll-top">
            <a href="javascript:void(0);" id="scrollTop">
              <i className="ti-angle-up" />
            </a>
          </div>
          <div className="text-center">
            <p className="content">
               {date} - Design &amp; Develop By{" "}
              <Link to="https://cloudstarmohan.github.io" target="_blank">
                Cloudstar Mohan (Mohan Shanmugam)
              </Link>
            </p>
          </div>
        </div>
      </footer>
    </>
  );
};
