import { galleryLists } from "./gallery";
import "./style.css";
export const GalleryPage = () => {
  return (
    <div className="row">
      {galleryLists.map((data) => (
        <div className="col-md-3">
          <div className="gallery">
            <div className="g-card">
                {
                    data?.tag?
                    <div className={`g-label ${data?.tagColor}`}>{data?.tag}</div>:null
                }
              <div className="g-image">
                <img
                  src={data?.image}
                  alt=""
                />
              </div>
              <div className="g-content">
                <p>
                {data?.title}
                </p>
              </div>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};
