import { useSelector } from "react-redux";
import { Link } from "react-router-dom";

export const AboutPage: React.FC = () => {
  const data = useSelector((state: any) => state.profile);
  return (
    <div className="container">
      <div className="row">
        <div className="col-12">
          <div className="title-bordered mb-5 d-flex align-items-center">
            <h1 className="h4">Hi, I Am {data?.name}</h1>
            <ul className="list-inline social-icons ml-auto mr-3">
              <li>
                <Link
                  to={
                    "https://cloudstarmohan.wordpress.com?ref_src=cloudstarmohan.github.com"
                  }
                  target="_blank"
                >
                  <i className="ti-wordpress" />
                </Link>
              </li>

              <li>
                <Link to={"https://github.com/spmohansp"} target="_blank">
                  <i className="ti-github" />
                </Link>
              </li>
              <li>
                <Link
                  to={"https://www.facebook.com/cloudstarmohan"}
                  target="_blank"
                >
                  <i className="ti-facebook" />
                </Link>
              </li>
              <li>
                <Link
                  to={"https://www.twitter.com/cloudstarmohan"}
                  target="_blank"
                >
                  <i className="ti-twitter" />
                </Link>
              </li>
              <li>
                <Link
                  to={"http://ca.linkedin.com/in/cloudstarmohan"}
                  target="_blank"
                >
                  <i className="ti-linkedin" />
                </Link>
              </li>
              <li>
                <Link
                  to={"https://www.instagram.com/cloudstarmohan/"}
                  target="_blank"
                >
                  <i className="ti-instagram" />
                </Link>
              </li>
              <li>
                <Link to={"https://bit.ly/Padikattugal/"} target="_blank">
                  <i className="ti-youtube" />
                </Link>
              </li>
              <li>
                <Link
                  to={"https://cloudstarmohan.blogspot.com/"}
                  target="_blank"
                >
                  <i className="ti-info-alt" />
                </Link>
              </li>
              <li>
                <Link
                  to={"https://in.pinterest.com/cloudstarmohan"}
                  target="_blank"
                >
                  <i className="ti-pinterest" />
                </Link>
              </li>
              <li>
                <Link to={"https://medium.com/@cloudstarmohan"} target="_blank">
                  <i className="ti-medium" />
                </Link>
              </li>
            </ul>
          </div>
          <img
            src={`${data?.icon}`}
            className="img-fluid w-100 mb-4 rounded-lg"
            style={{ height: "350px" }}
            alt={`${data?.name}`}
          />
          <div className="content">
            <p>{data?.description}</p>
            <hr />
          </div>
        </div>
      </div>
    </div>
  );
};
