import "./App.css";

import axios from "axios";

import { AppRouter } from "./routing";
import { createContext, useEffect, useState } from "react";
import { useAppDispatch } from "./store";
import { getDomainProfile } from "./store/profile";

// axios.defaults.baseURL = process.env.REACT_WORDPRESS_URL;
axios.defaults.baseURL = "https://public-api.wordpress.com/rest/v1.1/";
const initState = {
  domain:"cloudstarmohan",
}

export const WebsiteContext = createContext<{domain: string,setDomain?: any}>(initState);

export const App = () => {
  const [domain, setDomain] = useState<string>("cloudstarmohan");
  // console.log(process.env.WORDPRESS_URL);
  const dispatch = useAppDispatch()


  useEffect(()=>{
    dispatch(getDomainProfile(domain))
  },[domain])
  
  return (
    <WebsiteContext.Provider value={{ domain, setDomain }}>
      <AppRouter />
    </WebsiteContext.Provider>
  );
};
