import { Dispatch } from "react";
import { AppThunk } from "..";
import { getDomainProfileDetail } from "../api";
import { profileSlice, updateProfile } from "./reducer";

export const getDomainProfile =
  (domain: string): AppThunk =>
  (dispatch, getState) => {
    getWordPressProfile(domain, dispatch);
  };

export const getWordPressProfile = async (
  domain: string,
  dispatch: Dispatch<any>
): Promise<void> => {
  try {
    const data = await getDomainProfileDetail(domain);
    // updateProfile(data)(dispatch)
    dispatch(updateProfile(data));
  } catch (error) {
    // console.log("Error")
  }
};
