import axios from "axios";
import { min, split } from "lodash";
import { useContext, useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { DateTime } from "ts-luxon";

import { WebsiteContext } from "../../App";
import { PostDetail } from "../blogs/interface";

export const BlogDetailPage: React.FC = () => {
  const location = useParams();
  const website = useContext(WebsiteContext);
  const [post, setPost] = useState<PostDetail | null>(null);

  useEffect(() => {
    if (!website?.domain) return;
    getBlogDetail();
  }, [website]);

  const getBlogDetail = async (newData?: string) => {
    const data = await axios.get(
      `sites/${website?.domain}.wordpress.com/posts/slug:${
        newData ? newData : location?.id
      }`
    );
    setPost(data?.data);
  };

  function countMatchingCharacters(url1: string, url2: string) {
    const minLength = min([url1.length, url2.length]) || 0;
    let count = 0;
    for (let i = 0; i < minLength; i++) {
      if (url1[i] === url2[i]) {
        count++;
      } else {
        break;
      }
    }
    return count;
  }

  const handleClick = (event: any) => {
    event.preventDefault();
    const url = event?.target?.href;
    const matchCount = countMatchingCharacters(
      `https://${website?.domain}.wordpress.com`,
      url
    );
    if (!url) return;
    if (matchCount < 5) window.open(url, "_blank", "noreferrer");
    var lastChar = url[url.length - 1];
    const count = lastChar === "/" ? 2 : 1;
    const explodeData = split(url, "/");
    const searchParams = explodeData[explodeData?.length - count];
    // getBlogDetail(searchParams)
  };

  if (!post) return <p>Loading...</p>;

  return (
    <div className="container">
      <article className="row mb-4">
        <div className="col-lg-10 mx-auto mb-4">
          <h1 className="h2 mb-3">{post?.title}</h1>
          <ul className="list-inline post-meta mb-3">
            <li className="list-inline-item">
              <i className="ti-user mr-2" />
              <Link to="/about">{post?.author?.name}</Link>
            </li>
            <li className="list-inline-item">
              Date :{" "}
              {DateTime.fromISO(post?.date).toFormat("dd-MM-yyyy HH:mm a")}
            </li>
            <li className="list-inline-item">
              Categories :{" "}
              {Object.values(post?.categories || [])?.map((category) => (
                <Link
                  to={`/category/${category?.slug}`}
                  key={category?.name}
                  className="ml-1"
                >
                  {category?.name}
                </Link>
              ))}
            </li>
          </ul>
        </div>
        {/* <div className="col-12 mb-3">
            <div className="post-slider">
              <img
                src="images/post/post-2.jpg"
                className="img-fluid"
                alt="post-thumb"
              />
              <img
                src="images/post/post-4.jpg"
                className="img-fluid"
                alt="post-thumb"
              />
            </div>
          </div> */}
        <div className="col-lg-10 mx-auto">
          <div className="content">
            <p
              dangerouslySetInnerHTML={{ __html: post?.content || "" }}
              onClick={handleClick}
            ></p>
          </div>
        </div>
      </article>
    </div>
  );
};
