import axios from "axios";
import { useContext, useEffect, useState } from "react";
import { WebsiteContext } from "../../App";
import { Categories } from "../blogs/interface";
import { Link } from "react-router-dom";
interface LeftPanelProps {
  setFilter: React.Dispatch<
    React.SetStateAction<
      | {
          [key: string]: string;
        }
      | undefined
    >
  >;
}

export const LeftPanel: React.FC<LeftPanelProps> = ({ setFilter }) => {
  const website = useContext(WebsiteContext);
  const [categories, setCategories] = useState<Categories[]>([]);
  const [search, setSearch] = useState<string>("");

  useEffect(() => {
    getSiteCategory();
    getSiteTag();
  }, [website]);

  const getSiteCategory = async () => {
    const data = await axios.get(
      `sites/${website?.domain}.wordpress.com/categories`
    );
    setCategories(data?.data?.categories);
    // setCategories(data?.data)
  };

  const getSiteTag = async () => {
    const data = await axios.get(
      `sites/${website?.domain}.wordpress.com/pages`
    );
    // setCategories(data?.data?.categories)
    console.log(data?.data);
  };

  const SearchFilter = () => {
    setFilter((prev) => ({ ...prev, search }));
  };

  return (
    <aside className="col-lg-4 order-2 order-lg-1">
      {/* Search */}
      <div className="widget">
        <h5 className="widget-title">
          <span>Search</span>
        </h5>
        <form action="" className="widget-search">
          <input
            id="search-query"
            onChange={(e) => setSearch(e?.target?.value as string)}
            name="search"
            type="search"
            placeholder="Type & Hit Enter..."
          />
          <button type="button" onClick={SearchFilter}>
            <i className="ti-search" />
          </button>
        </form>
      </div>
      <div className="widget">
        <h5 className="widget-title">
          <span>Categories</span>
        </h5>
        <ul className="list-unstyled widget-list">
          {!categories?.length ? "Loading.." : ""}
          {categories?.map((category) =>
            category?.post_count ? (
              <li>
                <Link
                  to={`/category/${category?.slug}`}
                  key={category?.name}
                  className="d-flex"
                >
                  {category?.name}
                  <small className="ml-auto">({category?.post_count})</small>
                </Link>
              </li>
            ) : null
          )}
        </ul>
      </div>
      {/* tags */}
      {/* <div className="widget">
          <h5 className="widget-title"><span>Tags</span></h5>
          <ul className="list-inline widget-list-inline">
            <li className="list-inline-item"><a href="#!">Booth</a>
            </li>
            <li className="list-inline-item"><a href="#!">City</a>
            </li>
            <li className="list-inline-item"><a href="#!">Image</a>
            </li>
            <li className="list-inline-item"><a href="#!">New</a>
            </li>
            <li className="list-inline-item"><a href="#!">Photo</a>
            </li>
            <li className="list-inline-item"><a href="#!">Seasone</a>
            </li>
            <li className="list-inline-item"><a href="#!">Video</a>
            </li>
          </ul>
        </div> */}
      {/* latest post */}
      <div className="widget">
        <h5 className="widget-title">
          <span>Latest Article</span>
        </h5>
        {/* <ul className="list-unstyled widget-list">
          <li className="media widget-post align-items-center">
            <a href="post-elements.html">
              <img
                loading="lazy"
                className="mr-3"
                src="images/post/post-6.jpg"
              />
            </a>
            <div className="media-body">
              <h5 className="h6 mb-0">
                <a href="post-elements.html">
                  Elements That You Can Use To Create A New Post On This
                  Template.
                </a>
              </h5>
              <small>March 15, 2020</small>
            </div>
          </li>
        </ul> */}
      </div>
    </aside>
  );
};
