import { useContext, useState } from "react";

import { WebsiteContext } from "../App";

export const NewsLetter = () => {
  const [domain, setDomain] = useState<string>("");
  const website = useContext(WebsiteContext);

  const changeDomain = () => {
    website.setDomain(domain as string);
    setDomain("");
  };

  return (
    <>
      <div className="position-relative">
        <i className="ti-globe email-icon" />
        <input
          type="text"
          className="form-control"
          placeholder="Your Domain"
          value={domain}
          onChange={(e) => setDomain(e.target.value as string)}
        />
      </div>
      <button
        className="btn btn-primary btn-block rounded"
        type="button"
        onClick={changeDomain}
      >
        Get {domain} Domain
      </button>
    </>
  );
};
